import React, { useState, useEffect } from "react";
import TopHeader from "./Components/TopHeader";
import "./Components/app.css";

function App() {
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 2100);
  }, []);

  return (
    <>
      {loading ? (
        <p className="loader">Sohna Tax Consultancy</p>
      ) : (
        <TopHeader />
      )}
    </>
  );
}

export default App;
